.breadcrumbs--catalog{
  padding: 8px 0;
  background-color: $grayBg2;
}

.catalog{
  padding: 33px 0 60px;
  &__top{
    border-top: 1px solid #D5D5D5;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 30px;
  }
  &__grid,
  &__grid2,
  &__grid3{
    margin-top: 30px;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 15px));
    gap: 20px;
  }
  &__grid2{
    grid-template-columns: repeat(2,calc(50% - 10px));
  }
  &__grid3{
    grid-template-columns: repeat(3,calc(100% / 3 - 40px / 3));
  }
}

.catalog4{
  background-color: $grayBg;
}

.years-filter{
  display: flex;
  align-items: center;
  &__title,
  &__link{
    font-size: 16px;
    line-height: 160%;
    color: $gray;
    &:not(:last-child){
      margin-right: 40px;
    }
  }
  &__links{
    display: flex;
    align-items: center;
  }
  &__link{
    padding-bottom: 1px;
    border-bottom: 3px solid transparent;
    cursor: pointer;
    &:hover,
    &._active{
      border-color: $primary;
      color: $black;
    }
  }
}

.catalog-card{
  display: block;
  position: relative;
  &._big{
    grid-column: span 2;
    .catalog-card__img{
      padding-bottom: 51%;
    }
  }
  &:not(.catalog-card2):hover{
    .catalog-card__title{
      background-color: $primary;
    }
  }
  &__img{
    min-height: 100%;
    @include imgCover(104%);
    &:before{
      content: '';
      position: absolute;
      inset: 0;
      background: linear-gradient(179.45deg, rgba(0, 0, 0, 0) 48.38%, rgba(0, 0, 0, 0.8) 99.16%);
      z-index: 2;
    }
  }
  &__title{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 32px 38px;
    z-index: 3;
    color: #fff;
    font-size: 30px;
    line-height: 127%;
    transition: all .3s;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-shrink: 0;
    img{
      width: 15px;
    }
  }
  &__arrow{
    flex-shrink: 0;
    position: absolute;
    right: 30px;
    bottom: 30px;
    z-index: 3;
    width: 50px;
    height: 50px;
    @include center;
    border-radius: 50%;
    background-color: $primary;
    opacity: 0;
    transition: all .3s;
    img{
      width: 15px;
    }
  }
}

.catalog-card2{
  &:hover{
    .catalog-card__arrow{
      opacity: 1;
    }
  }
  .catalog-card__title{
    bottom: unset;
    top: 0;
  }
  .catalog-card__img{
    padding-bottom: 51%;
    &:before{
      background: linear-gradient(359.45deg, rgba(0, 0, 0, 0) 64.53%, rgba(0, 0, 0, 0.8) 99.89%);
    }
  }
}

.catalog-card3{
  &:hover{
    .catalog-card__title{
      background-color:  transparent !important;
    }
    .catalog-card__arrow{
      opacity: 1;
    }
  }
  .catalog-card__arrow{
    position: static;
    flex-shrink: 0;
    margin-left: 10px;
  }
}

.catalog-card4{
  padding: 20px;
  background: #FFFFFF;
  &:hover{
    .catalog-card__arrow{
      background: $primary;
    }
  }
  .catalog-card__img{
    min-height: unset;
    padding-bottom: 48%;
    &:before{
      display: none;
    }
    img{
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      width: unset;
      height: unset;
      max-width: 100%;
      max-height: 100%;
    }
  }
  .catalog-card__title{
    position: static;
    color: $black;
    background-color: transparent !important;
    padding: 0;
    margin-top: 8px;
  }
  .catalog-card__arrow{
    opacity: 1;
    position: static;
    background: #A9A9A9;
  }
}

.projects-page{
  .catalog-card__img{
    padding-bottom: 62.8% !important;
  }
}



@media screen and (max-width: 1500px){
  .catalog{
    &__grid3{
      grid-template-columns: repeat(2,calc(50% - 10px));
    }
  }
}


@media screen and (max-width: 1200px){
  .catalog-card{
    &__title{
      padding: 15px 20px;
      font-size: 24px;
    }
  }
}


@media screen and (max-width: 1000px){
  .catalog{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 10px));
    }
    &__grid2,
    &__grid3{
      grid-template-columns: 100%;
    }
  }
}


@media screen and (max-width: 800px){
  .catalog{
    padding-top: 15px;
    &__top{
      display: block;
      padding-top: 15px;
    }
    &__grid{
      grid-template-columns: 100%;
      margin-top: 15px;
    }
    &__grid2{
      margin-top: 15px;
    }
  }

  .years-filter{
    margin-top: 20px;
    &__title,
    &__link{
      &:not(:last-child){
        margin-right: 20px;
      }
    }
  }

  .catalog-card{
    grid-column: 1/-1 !important;
    &__img{
      padding-bottom: 70% !important;
    }
  }
}


@media screen and (max-width: 500px){
  .years-filter{
    display: block;
    &__title{
      margin: 0 0 10px;
    }
  }

  .catalog-card{
    &__title{
      background-color: $primary;
      font-size: 16px;
      padding-left: 10px;
      padding-right: 10px;
    }
    &__arrow{
      opacity: 1;
      width: 35px;
      height: 35px;
      img{
        width: 12px;
      }
    }
  }

  .catalog-card2{
    .catalog-card__title{
      background-color: transparent;
    }
  }

  .catalog-card4{
    padding: 10px;
  }
}
