@import 'vars';
html{
  height: 100%;
  width: calc(100vw - (100vw - 100%));
  scroll-behavior: smooth;
}

.main{
  padding-top: 135px;
}

.ordinary-link{
  transition: all .3s;
  &:hover{
    color: $primary;
  }
}


@media screen and (max-width: 1200px){
  .main{
    padding-top: 90px;
  }
}

@media screen and (max-width: 500px){
  .main{
    padding-top: 66px;
  }
}
