@import "../vars.scss";
.breadcrumbs {
  padding: 10px 0 40px;
  &__item {
    font-weight: 500;
    font-size: 13px;
    line-height: 16px;
    position: relative;
    color: #595959;
    display: inline;
    &:hover,
    &:last-child {
      color: $primary;
    }
    &:not(:first-child) {
      margin-left: 38px;
      &:before {
        content: '';
        position: absolute;
        z-index: 1;
        width: 8px;
        height: 8px;
        background-image: url(../images/bread-arrow.svg);
        @include bgImgSize(100%);
        top: 5px;
        right: 100%;
        margin-right: 15px;
      }
    }
  }
}


@media screen and (max-width: 1200px){
  .breadcrumbs{
    padding-bottom: 20px;
    &__item{
      font-size: 12px;
      &:not(:first-child) {
        margin-left: 25px;
        &:before{
          margin-right: 10px;
          top: 4px;
        }
      }
    }
  }
}
