.m-select{
  select{
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    &::-ms-expand {
      display: none; // IE
    }

    background-image: url(../images/arrow-down2.svg);
    background-size: 8px auto;
    background-repeat: no-repeat;
    background-position: right 16px center;
    padding: 0 30px 0 20px;
    border: 1px solid #D1D1D1;
    border-radius: 0 0 0 15px;
    height: 36px;
  }
}

.cartcalc{
  display: flex;
  align-items: center;
  width: 100px;
  height: 64px;
  padding: 0 17.5px;
  border: 1px solid #C6C6C6;
  border-radius: 50px;
  button{
    font-size: 18px;
    line-height: 160%;
    flex-shrink: 0;
    width: 11px;
    background-color: transparent;
    @include center;
  }
  input{
    width: calc(100% - 22px);
    flex-grow: 1;
    text-align: center;
    font-size: 18px;
  }
}
