.catalog-item{
  overflow: hidden;
  position: relative;
  margin-bottom: 30px;
  &:before{
    content: '';
    z-index: 2;
    position: absolute;
    height: 100%;
    width: calc(((100% - 1700px ) / 2) + (1700px * .328));
    left: 0;
    top: 0;
    background: #F4F4F4;
    border-radius: 0 0 0 60px;
  }
  &__grid{
    display: grid;
    grid-template-columns: 32.8% calc(67.2% - 20px);
    gap: 20px;
  }
  &__info{
    padding: 47px 0 59px;
    position: relative;
    z-index: 3;
  }
  &__wrap{
    position: relative;
    z-index: 2;
    //padding-left: 16.618%;
    padding-right: 44px;
  }
  .variants,
  &__top{
    &:before{
      content: '';
      position: absolute;
      width: 10000px;
      height: 1px;
      right: 0;
      bottom: 0;
      border-bottom: $border;
    }
  }
  &__top{
    padding-right: 25px;
    &._pb70{
      padding-bottom: 70px;
    }
    .arrow-link{
      margin-top: 23px;
      padding: 30px 30px 30px 0;
    }
  }
  &__text{
    margin-top: 63px;
    max-width: 473px;
  }
}

._show-variants{
  ._view1{
    display: none;
  }
  img{
    transition: all .3s;
  }
  &:not(._active){
    ._view1{
      display: block;
    }
    ._view2{
      display: none;
    }
    img{
      transform: rotate(-180deg);
    }
  }
}

.arrow-link{
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: all .3s;
  img{
    width: 15px;
    margin-left: 10px;
  }
  &:hover{
    cursor: pointer;
    color: $primary;
  }
}

.variants{
  padding-top: 38px;
  padding-bottom: 27px;
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-pagination{
      position: static;
      width: auto;
    }
    &-button{
      position: static;
      margin: 0;
      flex-shrink: 0;
    }
  }
  &__nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 26px;
  }
}

.variant-card{
  display: block;
  color: $black;
  &._active,
  &:hover{
    .variant-card__img{
      border-color: $primary;
    }
  }
  &__img{
    transition: all .3s;
    padding-bottom: 100%;
    position: relative;
    background-color: #fff;
    border-radius: 20px;
    border: 1px solid #fff;
    overflow: hidden;
    img{
      @include centerAbsolute;
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__title{
    text-align: center;
    margin-top: 13px;
  }
}

.docs{
  padding-top: 59px;
  &__link{
    color: $black;
    display: flex;
    align-items: center;
    font-size: 18px;
    line-height: 160%;
    max-width: 417px;
    &:not(:last-child){
      margin-bottom: 40px;
    }
    &-img{
      width: 30px;
      flex-shrink: 0;
      margin-right: 30px;
      img{
        max-width: 100%;
        display: block;
      }
    }
    &-title{
      flex-grow: 1;
    }
    &-format{
      width: 43px;
      flex-shrink: 0;
      padding-left: 10px;
    }
  }
}

.gallery{
  .swiper{
    overflow: unset;
    &-slide{
      width: unset;
    }
    &-button{
      border-radius: 50%;
      height: 50px;
      top: unset;
      bottom: 13px;
      margin: 0;
      img{
        width: 22px;
      }
      &-prev{
        left: 17px;
      }
      &-next{
        right: calc((100vw - 1700px) / 2 * -1 + 13px);
      }
    }
  }
  &__small{
    margin-top: 20px;
    .swiper-slide{
      opacity: .3;
    }
    .swiper-slide-thumb-active{
      opacity: 1;
    }
  }
  &__big{
    .gallery__img{
      height: 700px;
      img{
        border-radius: 0 0 0 50px;
      }
    }
  }
  &__img{
    display: block;
    height: 106px;
    img{
      height: 100%;
      width: auto;
      border-radius: 0 0 0 25px;
    }
  }
}

.about{
  padding: 30px 0;
  &__grid{
    margin-top: 58px;
    display: grid;
    grid-template-columns: 41.2% calc(58.8% - 160px);
    gap: 30px 160px;
  }
  &__img{
    img{
      width: 100%;
      border-bottom-left-radius: 50px;
    }
  }
  &__content{
    align-self: center;
  }
}

.block-top{
  border-bottom: $border;
  padding-bottom: 28px;
}

.models{
  padding: 30px 0;
  position: relative;
  .same__squirrel{
    top: -60px;
  }
}

.model{
  position: relative;
  overflow: hidden;
  padding-bottom: 1px;
  &:before{
    content: '';
    position: absolute;
    top: 139px;
    left: 0;
    border-bottom: $border;
    width: 100%;
  }
  &__grid{
    display: grid;
    grid-template-columns: 66% calc(34% - 30px);
    gap: 30px;
  }
}

.model-media{
  &__top{
    padding-top: 25px;
    height: 140px;
    display: flex;
    flex-direction: column;
  }
  &__info{
    margin-top: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 1065px;
    padding-top: 21px;
  }
  &__articul{
    font-weight: 500;
    font-size: 15px;
    line-height: 19px;
    text-transform: uppercase;
    color: #484848;
  }
  &__content{
    margin-top: 23px;
  }
  &__img{
    padding-bottom: 45%;
    position: relative;
    img{
      @include centerAbsolute;
      max-width: calc(100% - 30px);
      max-height: 100%;
      display: block;
    }
    &._bg{
      background: #F0F6F6;
      border-radius: 0 0 0 60px;
    }
  }
}

.model-info{
  position: relative;
  padding-bottom: 1px;
  &:before{
    content: '';
    position: absolute;
    width: 10000px;
    height: 100%;
    border-width: 0 0 1px 1px;
    border-style: solid;
    border-color: #D4D4D4;
    border-radius: 0 0 0 60px;
    pointer-events: none;
  }
  &__top{
    height: 140px;
    display: flex;
    align-items: center;
    padding-left: 60px;
    .subtitle{
      color: $gray;
    }
  }
  &__wrap{
    padding-left: 60px;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 10000px;
      height: 1px;
      left: 0;
      bottom: 0;
      border-bottom: $border;
    }
  }
  &__wrap1{
    padding-bottom: 60px;
  }
  &__wrap2{
    padding-top: 30px;
    padding-bottom: 15px;
  }
  &__wrap3{
    padding-top: 15px;
    padding-bottom: 27px;
    &:before{
      opacity: 0;
    }
  }
  &__title{
    margin-top: 60px;
    color: $gray;
  }
}

.model-config{
  padding-top: 24px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  &__title{
    margin-right: 40px;
  }
  &__tab{
    margin-right: 40px;
    position: relative;
    input{
      opacity: 0;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 2;
      cursor: pointer;
      &:checked+label{
        border-color: $primary;
        color: $black;
      }
    }
    label{
      transition: all .3s;
      color: $gray;
      padding: 5px 0 2px;
      font-size: 16px;
      line-height: 160%;
      border-bottom: 3px solid transparent;
    }
  }
}

.model-material{
  display: flex;
  align-items: center;
  margin-top: 24px;
  &__title{
    margin-right: 40px;
  }
}

.model-props{
  display: grid;
  grid-template-columns: 1fr 1.05fr;
  &__nav{
    margin-top: 27px;
    display: flex;
    justify-content: flex-end;
  }
  &__more{
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 14px;
    line-height: 160%;
    color: $gray;
    background-color: transparent;
    &:hover{
      color: $primary;
    }
    img{
      width: 26px;
      display: block;
      flex-shrink: 0;
      margin-right: 15px;
      transition: all .3s;
    }
    ._view2{
      display: none;
    }
    &._active{
      img{
        transform: rotate(-45deg);
      }
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
  &:not(._active){
    .model-prop{
      &:nth-child(4)~.model-prop{
        display: none;
      }
    }
  }

}

.model-prop{
  padding-top: 6px;
  padding-bottom: 6px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 14px;
  line-height: 160%;
  @media screen and (min-width: 1401px){
    &:nth-child(4n - 3),
    &:nth-child(4n - 2){
      background: #F4F4F4;
    }
    &:nth-child(odd){
      padding-left: 20px;
      padding-right: 30px;
    }
    &:nth-child(even){
      padding-right: 20px;
      padding-left: 30px;
    }
  }
  &__desc{
    text-align: right;
  }
}

.model-price{
  font-weight: 500;
  font-size: 16px;
  line-height: 125%;
  &__num{
    font-size: 30px;
    line-height: 127%;
    text-transform: uppercase;
    margin-right: 2px;
  }
}

.model-nav{
  display: flex;
  margin-top: 12px;
  .cartcalc{
    flex-shrink: 0;
  }
  &__btn{
    margin-left: 9px;
    flex-grow: 1;
    img{
      width: 20px;
      right: 25px;
    }
  }
}

.same{
  padding: 53px 0;
  margin-top: 50px;
  background: $grayBg;
  position: relative;
  &__wrap{
    margin-top: 30px;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 40px / 3));
    gap: 20px;
  }
  &__squirrel{
    position: absolute;
    border: none;
    width: 190px;
    right: -20px;
    top: 75px;
    transform: rotate(-30deg);
  }
  @media screen and (min-width: 1921px){
    &__squirrel{
      right: calc((100% - 1700px) / 2 - 90px);
    }
  }
}

.same-card{
  padding: 22px 20px 22px 47px;
  background: #FFFFFF;
  border-radius: 0 0 0 60px;
  display: block;
  &:hover{
    .same-card__btn{
      background: $primary;
    }
  }
  &__img{
    position: relative;
    padding-bottom: 54%;
    img{
      @include centerAbsolute;
      max-width: calc(100% - 40px);
      max-height: 100%;
      display: block;
    }
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 5px;
  }
  &__title{
    font-size: 24px;
    line-height: 125%;
  }
  &__btn{
    transition: all .3s;
    flex-shrink: 0;
    width: 50px;
    height: 50px;
    background: #A9A9A9;
    border-radius: 50%;
    @include center;
    img{
      width: 16px;
      display: block;
    }
  }
}


@media screen and (max-width: 1920px){
  .catalog-item{
    &:before{
      width: calc(45px + (1700px * .328));
      display: none;
    }
    &__info{
      &:before{
        content: '';
        z-index: 2;
        position: absolute;
        height: 100%;
        width: calc(100% + ((100vw - 1700px) / 2));
        right: 0;
        top: 0;
        background: #F4F4F4;
        border-radius: 0 0 0 60px;
      }
    }
  }

  .same{
    overflow: hidden;
    &__squirrel{
      right: -20px;
    }
  }
}

@media screen and (max-width: 1730px){
  .catalog-item{
    &__info{
      &:before{
        width: calc(100% + 15px);
      }
    }
  }

  .gallery{
    .swiper{
      &-button{
        &-next{
          right: -2px;
        }
      }
    }
  }
}


@media screen and (max-width: 1700px){
  .gallery{
    &__big{
      .gallery__img{
        height: 650px;
      }
    }
  }

  .model{
    &__grid{
      grid-template-columns: 60% calc(40% - 30px);
    }
  }
}


@media screen and (max-width: 1530px){
  .catalog-item{
    &__info{
      padding: 20px 0;
    }
    &__wrap{
      padding-right: 20px;
    }
    .arrow-link{
      margin-top: 0;
    }
    &__text{
      margin-top: 20px;
    }
    &__top{
      &._pb70{
        padding-bottom: 20px;
      }
    }
  }

  .variants{
    padding: 20px 20px 20px 0;
  }

  .docs{
    padding-top: 20px;
    &__link{
      &:not(:last-child){
        margin-bottom: 20px;
      }
    }
  }

  .gallery{
    &__big{
      .gallery__img{
        height: 500px;
      }
    }
  }

  .about{
    &__grid{
      grid-template-columns: 40% calc(60% - 30px);
      gap: 30px;
    }
  }

  .model-media{
    &__content{
      margin-top: 60px;
    }
  }

  .model-info{
    &__wrap{
      padding-left: 30px;
    }
  }
}


@media screen and (max-width: 1400px){
  .model-props{
    grid-template-columns: 100%;
  }

  .model-prop{
    padding-left: 20px;
    padding-right: 20px;
    &:nth-child(odd){
      background: #F4F4F4;
    }
  }

  .same{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 10px));
    }
  }
}


@media screen and (max-width: 1200px){
  .catalog-item{
    &:before{
      display: none;
    }
    &__grid{
      grid-template-columns: 100%;
    }
    &__info{
      &:before{
        display: none;
      }
    }
    &__top,
    .variants{
      &:before{
        right: -15px;
        width: calc(100% + 30px);
      }
    }
    &__wrap{
      padding-right: 0;
    }
    .arrow-link{
      padding-right: 0;
    }
  }

  .variants{
    .swiper{
      &-container{
        overflow: unset;
      }
      &-slide{
        width: 150px;
      }
    }
  }

  .model{
    &:before{
      display: none;
    }
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .model-media{
    &__top{
      height: auto;
      width: calc(100% + 30px);
      margin-left: -15px;
      border-bottom: $border;
      padding-left: 15px;
      padding-right: 15px;
    }
    &__content{
      margin-top: 20px;
    }
  }

  .model-info{
    border: $border;
    border-radius: 0 0 0 30px;
    &:before{
      display: none;
    }
    &__top{
      height: auto;
      padding: 20px 15px;
    }
    &__wrap{
      padding-left: 15px;
      padding-right: 15px;
      &:before{
        width: 100%;
      }
    }
    &__wrap1{
      padding-bottom: 20px;
    }
    &__title{
      margin-top: 20px;
    }
  }

  .same{
    &__squirrel{
      display: none;
    }
  }
}


@media screen and (max-width: 992px){
  .gallery{
    &__small{
      display: none;
    }
    &__big{
      .gallery__img{
        height: calc(100vh / 3);
      }
    }
    .swiper{
      &-button{
        display: none;
      }
    }
  }

  .about{
    &__grid{
      margin-top: 30px;
      grid-template-columns: 100%;
    }
  }

  .same-card{
    padding: 20px;
    &__title{
      font-size: 20px;
    }
    &__btn{
      width: 40px;
      height: 40px;
      margin-left: 10px;
      img{
        width: 14px;
      }
    }
  }
}


@media screen and (max-width: 768px){
  .same{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}


@media screen and (max-width: 600px){
  .variants{
    .swiper{
      &-container{
        width: calc(100% + 10px);
      }
      &-slide{
        width: calc(50% - 10px);
      }
    }
  }

  .gallery{
    .swiper{
      &-slide{
        width: 95%;
      }
    }
    &__big{
      .gallery__img{
        height: auto;
        img{
          border-radius: 0 0 0 20px;
        }
      }
      a{
        display: block;
      }
    }
    &__img{
      @include imgCover(60%);
      display: block;
    }
  }

  .model-media{
    &__info{
      flex-direction: column;
      align-items: unset;
    }
    &__articul{
      margin: 0 0 25px;
      width: 100%;
      order: -1;
    }
    &__img{
      border-radius: 0 0 0 30px;
    }
  }

  .model-config{
    padding-top: 0;
    &__title{
      width: 100%;
      margin: 0 0 10px;
    }
  }

  .model-nav{
    flex-wrap: wrap;
    &__btn{
      width: 100%;
      margin: 5px 0 0;
    }
    .cartcalc{
      width: 100%;
    }
  }
}
