.text64{
  font-size: 64px;
  line-height: 125%;
}

.text48{
  font-size: 48px;
  line-height: 160%;
}

.text40{
  font-size: 40px;
  line-height: 125%;
}

.text32{
  font-size: 32px;
  line-height: 125%;
}

.text24{
  font-size: 24px;
  line-height: 125%;
}

.text20{
  font-size: 20px;
  line-height: 125%;
}

.text18{
  font-size: 18px;
  line-height: 160%;
  p{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 30px;
    }
  }
}

.text16{
  font-size: 16px;
  line-height: 160%;
}

.text13{
  font-size: 13px;
  line-height: 160%;
}

.subtitle{
  color: #A9A9A9;
  font-weight: 900;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.2em;
  text-transform: uppercase;
}

.accent-text{
  color: $primary;
}

.gray-text{
  color: $gray;
}

.medium-text{
  font-weight: 500;
}


@media screen and (max-width: 1700px){
  .text64{
    font-size: 54px;
  }
}


@media screen and (max-width: 1500px){
  .text64{
    font-size: 44px;
  }
}


@media screen and (max-width: 992px){
  .text48{
    font-size: 28px;
  }
}


@media screen and (max-width: 600px){
  .text64{
    font-size: 32px;
  }

  .text40{
    font-size: 28px;
  }

  .text32{
    font-size: 26px;
  }

  .text24{
    font-size: 20px;
  }

  .text20{
    font-size: 18px;
  }
}
