.order{
  position: fixed;
  top: 0;
  right: 0;
  z-index: 101;
  max-width: 100%;
  width: 585px;
  height: 100vh;
  overflow-y: auto;
  background: #FFFFFF;
  border-radius: 0 0 0 50px;
  display: flex;
  flex-direction: column;
  transition: all .6s;
  transform: translateX(30px);
  opacity: 0;
  pointer-events: none;
  &__bg{
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.7);
    z-index: 100;
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 30px 0 23px;
    border-bottom: 1px solid #D5D5D5;
    .tab{
      padding: 27px 30px;
      text-transform: uppercase;
      font-weight: 500;
      font-size: 12px;
      line-height: 15px;
      letter-spacing: 0.1em;
      &:after{
        display: none;
      }
    }
  }
  &__close{
    cursor: pointer;
    transform: scale(1.1);
  }
  .tab-content{
    flex-grow: 1;
    &._active{
      display: flex;
      flex-direction: column;
    }
  }
  &__bottom{
    padding: 32px 48px;
    margin-top: auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__hint{
    font-size: 22px;
    line-height: 140%;
  }
  &__btn{
    margin-top: 22px;
    width: 100%;
    height: 64px;
  }
}

.order-card{
  padding: 30px;
  border-bottom: 1px solid #D5D5D5;
  &__top{
    display: grid;
    grid-template-columns: 225px calc(100% - 253px);
    gap: 28px;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__info{
    padding-top: 8px;
  }
  &__articul{
    font-size: 14px;
    line-height: 129%;
    text-transform: uppercase;
    font-weight: 500;
    color: #484848;
    margin-top: 15px;
    &-num{
      color: $primary;
    }
  }
  &__nav{
    margin-top: 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .cartcalc{
      height: 46px;
    }
  }
  &__delete{
    width: 16px;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
  .model-props{
    margin-top: 18px;
  }
  .model-prop{
    display: flex !important;
  }
}

.order-form{
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  padding: 60px 50px 98px;
  .order__hint{
    text-align: center;
  }
  &__fields{
    @include grid100gap(20px);
    margin: 50px auto 0;
    width: 430px;
    max-width: 100%;
    .form__item{
      width: 100%;
      max-width: 100%;
      margin: 0 !important;
      input{
        max-width: unset;
      }
    }
  }
  &__bottom{
    margin: auto auto 0;
    width: 430px;
    max-width: 100%;
  }
  &__confirm{
    margin: 30px auto 0;
    text-align: center;
    width: 327px;
    max-width: 100%;
    font-size: 13px;
    line-height: 120%;
    color: #6C6C6C;
    a{
      text-decoration: underline;
    }
  }
}

.file-input{
  position: relative;
  display: flex;
  align-items: center;
  padding: 0 25px;
  font-size: 16px;
  line-height: 160%;
  input{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    cursor: pointer;
    opacity: 0;
  }
  span{
    color: $primary;
    border-bottom: 1px dashed $primary;
  }
  img{
    width: 23px;
    margin-right: 25px;
    flex-shrink: 0;
  }
}

._show-order{
  height: 100vh;
  overflow-y: hidden;
  .order{
    pointer-events: unset;
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
    &__bg{
      display: block;
    }
  }
}



@media screen and (max-width: 600px){
  .order{
    width: 100%;
    border-radius: 0;
  }

  .order-card{
    &__top{
      grid-template-columns: 100%;
    }
    &__img{
      max-width: 250px;
      margin: 0 auto;
    }
  }

  .order-form{
    padding: 30px;
  }
}
