.nav__popup{
  &.type2{
    .nav__popup-image{
      max-width: 485px;
      height: 510px;
      margin-right: 60px;
      img{
        height: 100%;
        object-fit: cover;
      }
    }
    .nav__popup-link{
      a{
        font-size: 16px;
        padding-top: 7px;
        padding-bottom: 7px;
      }
    }
    .nav__popup-item{
      &:not(:last-child){
        margin-right: 40px;
      }
    }
  }
}

.nav__popup-wrapper{
  justify-content: center;
}

.nav__popup-title{
  font-size: 24px;
  line-height: 125%;
  margin-bottom: 28px;
  padding-top: 24px;
  max-width: 250px;
}

.header-catalog{
  width: 670px;
  max-width: calc(100% - 680px);
  padding-top: 37px;
  position: relative;
  &__items{
    @include grid100gap(3.125em);
  }
  &__item{
    display: flex;
    align-items: center;
    &:hover{
      &:before{
        background-color: $primary;
        background-image: url(../images/arrow-right2.svg);
      }
    }
    &:before{
      content: '';
      display: block;
      flex-shrink: 0;
      background-image: url(../images/arrow-right.svg);
      @include bgImgSize(16px);
      width: 36px;
      height: 22px;
      margin-right: 10px;
      border-radius: 50px;
      transition: all .3s;
    }
    &-title{
      font-size: 24px;
      line-height: 125%;
      &:after{
        display: none !important;
      }
    }
    &._open{
      .submenu{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
}

.submenu{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: $grayBg2;
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  &__back{
    display: flex;
    align-items: center;
    padding: 0;
    background-color: transparent;
    font-size: 24px;
    line-height: 125%;
    transition: all .3s;
    span{
      &:after{
        display: none !important;
      }
    }
    img{
      display: block;
      margin-right: 20px;
      transform: translateY(3px);
    }
    &:hover{
      color: $primary;
    }
  }
  &__links{
    margin-top: 28px;
    padding-left: 36px;
  }
  .nav__popup-link{
    a{
      padding-top: 7px;
      padding-bottom: 7px;
    }
  }
}


@media screen and (max-width: 1700px){
  .nav__popup{
    padding-left: 15px;
    padding-right: 15px;
    &.type2{
      .nav__popup-image{
        margin-right: 20px;
        max-width: 350px;
      }
      .nav__popup-item{
        &:not(:last-child){
          margin-right: 20px;
        }
      }
    }
  }

  .nav__popup-title{
    padding-top: 0;
  }
}


@media screen and (max-width: 1400px){
  .nav__popup{
    &.type2{
      .nav__popup-wrapper{
        display: grid;
        grid-template-columns: 350px auto auto;
        gap: 40px;
        justify-content: unset;
      }
      .nav__popup-image{
        width: 100%;
        height: 50%;
        grid-row: 1/3;
        img{
          height: unset;
          min-height: 100%;
        }
      }
    }

    .nav__popup-item{
      margin: 0 !important;
    }
  }

  .header-catalog{
    padding-top: 0;
    &__items{
      gap: 2em;
    }
  }
}
