@import "components/text";
@import "components/input";
.swiper-button1{
  width: 50px;
  height: 22px;
  border-radius: 50px;
  background: #FF6719;
  &:after{
    display: none;
  }
  img{
    width: 16px;
    display: block;
  }
  &.swiper-button-disabled{
    opacity: 1;
    background: #CBCAC9;
  }
}


.swiper-pagination1{
  .swiper-pagination-bullet{
    width: 8px;
    height: 8px;
    margin: 0 6px;
    opacity: 1;
    background: #CCCCCC;
    &-active{
      background: $primary;
    }
  }
}

.tabs{
  display: flex;
  align-items: center;
}

.tab{
  font-size: 16px;
  line-height: 160%;
  color: #727272;
  padding: 0 15px 16px;
  text-align: center;
  min-width: 90px;
  position: relative;
  transition: all .3s;
  margin-right: 18px;
  cursor: pointer;
  &:before{
    content: '';
    position: absolute;
    bottom: -1px;
    left: 0;
    width: 100%;
    height: 3px;
    transition: inherit;
    background-color: $primary;
    opacity: 0;
  }
  &:after{
    content: '';
    position: absolute;
    opacity: 0;
    transition: inherit;
    width: 16px;
    height: 8.59px;
    left: 50%;
    top: 100%;
    transform: translate(-50%,-1px);
    background-image: url(../images/arrow-down.svg);
    @include bgImgSize(100%);
  }
  &._active{
    color: $black;
    pointer-events: none;
    &:after,
    &:before{
      opacity: 1;
    }
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.toggle-container {
  transition: height 0.35s ease-in-out;
  overflow: hidden;
}
.toggle-container:not(.active) {
  display: none;
}
