.same2{
  @media screen and (min-width: 1400px){
    .same__grid{
      grid-template-columns: repeat(4,calc(25% - 15px));
    }
  }
}

.project-item{
  .catalog-item__top:before{
    display: none;
  }
  &__squirrel{
    position: absolute;
    width: 155px;
    bottom: -37px;
    z-index: 2;
    left: -41px;
  }
}


@media screen and (max-width: 1600px){
  .project-item{
    &__squirrel{
      display: none;
    }
  }
}
