.materials{
  background-color: $grayBg;
  &__top{
    border-bottom: $border;
  }
  .tabs{
    margin-top: 40px;
  }
  .tab-content{
    margin-top: 56px;
  }
  &__groups{
    @include grid100gap(60px);
  }
}

.material-group{
  &__top{
    display: flex;
    align-items: flex-end;
  }
  &__desc{
    padding: 0 0 11px 26px;
    &:before{
      content: '|';
      display: inline-block;
      margin-right: 20px;
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 40px / 3));
    gap: 30px 20px;
    margin-top: 60px;
  }
}

.material-card{
  background-color: #fff;
  border-radius: 100px 100px 0 0;
  &__img{
    @include imgCover(36.1%);
    img{
      border-radius: 100px;
      display: block;
    }
  }
  &__content{
    padding: 20px 40px 32px;
  }
  &__title{
    font-size: 30px;
    line-height: 127%;
  }
  &__desc{
    font-size: 16px;
    line-height: 160%;
    color: #5B5B5B;
    margin-top: 15px;
  }
}

.material-card2{
  text-align: center;
  background-color: transparent;
  border-radius: 0;
  .material-card__title{
    margin-top: 20px;
  }
}


@media screen and (max-width: 992px){
  .materials{
    .tabs{
      margin-top: 30px;
    }
  }

  .material-group{
    &__top{
      display: block;
    }
    &__desc{
      padding: 10px 0 0;
      &:before{
        display: none;
      }
    }
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 10px));
      margin-top: 30px;
    }
  }

  .material-card{
    &__content{
      padding: 20px;
    }
    &__title{
      font-size: 27px;
    }
    &__desc{
      font-size: 14px;
      margin-top: 10px;
    }
  }
}


@media screen and (max-width: 600px){
  .material-group{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
