.about-page{
  position: relative;
  z-index: 2;
  background-color: $grayBg;
  border-radius: 0 0 0 50px;
  &__grid{
    margin-top: 116px;
    display: grid;
    grid-template-columns: 27.9% calc(72.1% - 104px);
    justify-content: space-between;
    gap: 32px;
  }
  &__content{
    padding-top: 19%;
  }
  &__media{
    position: relative;
  }
  &__img{
    & img{
      width: 100%;
      display: block;
      border-radius: 0 0 0 50px;
    }
  }
  &__logo{
    position: absolute;
    bottom: 100%;
    right: 0;
    max-width: 100%;
    width: 947px;
  }
  &__squirrel{
    position: absolute;
    top: -81px;
    right: 71.5%;
    width: 135px;
  }
}

.work-with{
  margin-top: -96px;
  padding: 125px 0 44px;
  background-color: $primary;
  color: #fff;
  text-transform: uppercase;
  &__inner{
    max-width: 1376px;
  }
  &__title{
    margin-top: -6px;
  }
  &__desc{
    margin: -19px 0 0;
    text-align: right;
  }
  .text20{
    line-height: 160%;
  }
}

.clients{
  padding: 30px 0;
  &__slider{
    margin-top: 30px;
    position: relative;
  }
  .swiper{
    &-button1{
      top: 50%;
      margin-top: 0;
      transform: translateY(-50%);
      width: 50px;
      height: 50px;
      border-radius: 50%;
      right: -50px;
      img{
        width: 22px;
      }
    }
    &-container{
      overflow: hidden;
      width: calc(100% + 20px);
    }
    &-wrapper{
      align-items: center;
    }
    &-slide{
      width: calc(100% / 8);
      padding-right: 20px;
      img{
        width: 100%;
        display: block;
      }
    }
    &-pagination{
      position: static;
      margin-top: 67px;
      height: 10px;
      display: flex;
      align-items: center;
      justify-content: center;
      &-bullet{
        flex-shrink: 0;
        width: 6px;
        height: 6px;
        opacity: 1;
        background: #272727;
        margin: 0 15px !important;
        position: relative;
        &:before{
          content: '';
          @include centerAbsolute;
          border: 2px solid #272727;
          width: 30px;
          height: 30px;
          border-radius: 50%;
          opacity: 0;
          transition: inherit;
        }
        &-active{
          width: 10px;
          height: 10px;
          &:before{
            opacity: 1;
          }
        }
      }
    }
  }
}

.counts,
.team,
.how-work,
.production{
  &__top{
    padding-bottom: 28px;
    border-bottom: 1px solid #D5D5D5;
  }
}

.team{
  padding: 40px 0;
  overflow: hidden;
  &__info{
    margin-top: 30px;
    max-width: 1190px;
  }
  &__title{
    text-transform: uppercase;
    span{
      display: block;
    }
    &-bottom{
      text-align: right;
      margin-top: -16px;
      color: $primary;
    }
  }
  &__desc{
    text-align: right;
    text-transform: uppercase;
    margin-top: -6px;
  }
  &__grid{
    margin-top: 200px;
    display: grid;
    grid-template-columns: repeat(6,calc(100% / 6 - 100px / 6));
    gap: 100px 20px;
  }

  @media screen and (min-width: 1500px){
    .team-card{
      &:nth-child(1),
      &:nth-child(2){
        margin-top: -120px;
      }
      &:nth-child(1){
        .team-card__badge:before{
          width: calc(100% + 83px);
          left: unset;
          right: 0;
        }
      }
      &:nth-child(6){
        .team-card__badge{
          left: 0;
          &:before{
            width: calc(100% + 90px);
          }
          @media screen and (max-width: 1700px){
            left: unset;
            right: 0;
          }
        }
      }
    }
  }
}

.team-card{
  position: relative;
  &__badge{
    position: absolute;
    padding: 0 23px;
    height: 31px;
    display: flex;
    align-items: center;
    bottom: 100%;
    left: -23px;
    white-space: nowrap;
    margin-bottom: 29px;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: $primary;
    }
    .subtitle{
      position: relative;
      z-index: 2;
      color: #fff;
    }
  }
  &__img{
    @include imgCover(108.2%);
    img{
      border-radius: 0 0 0 50px;
    }
  }
  &__content{
    margin-top: 20px;
  }
  &__desc{
    color: #777777;
    margin-top: 3px;
  }
}

.counts{
  padding: 217px 0 30px;
  overflow: hidden;
  &__grid{
    display: grid;
    grid-template-columns: 44.2% 55.8%;
  }
  &__col1{
    padding-top: 17%;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 12px));
    gap: 63px 24px;
  }
  &__item{
    &-num{
      color: $primary;
      font-weight: 300;
      font-size: 72px;
      line-height: 127%;
    }
    &-text{
      font-size: 16px;
      line-height: 160%;
      color: rgba(0, 0, 0, 0.75);
    }
  }
  &__item2{
    display: flex;
    align-items: flex-end;
    &-num{
      height: 117px;
      color: $primary;
      font-weight: 700;
      font-size: 250px;
      margin-top: -127px;
      transform: translateY(-100%);
    }
    &-text{
      text-transform: uppercase;
    }
  }
  &__col2{
    position: relative;
    display: flex;
    align-items: flex-end;
    &:before{
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      height: 100%;
      width: 2000px;
      background-color: $primary;
      border-radius: 0 0 0 60px;
      z-index: -1;
    }
  }
  &__countries{
    padding: 0 0 0 42px;
    display: flex;
    align-items: flex-end;
    color: #fff;
    &-num{
      font-weight: 700;
      font-size: 250px;
      transform: translateY(10%);
      margin-right: 6px;
    }
    &-text{
      text-transform: uppercase;
      padding-bottom: 35px;
    }
  }
  &__img{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 185px;
    width: 591px;
    max-width: 75%;
  }
}

.how-work{
  padding: 30px 0;
  &__grid{
    margin-top: 180px;
    display: grid;
    grid-template-columns: repeat(6,calc(100% / 6 - 80px / 6));
    gap: 35px 16px;
  }
  &__result{
    img{
      width: 100%;
      display: block;
    }
    &-title{
      color: $primary;
      margin-top: 20px;
    }
  }
  @media screen and (min-width: 1500px){
    .step-card{
      &:nth-child(1){
        margin-top: -73px;
      }
      &:nth-child(3){
        margin-top: -40px;
      }
      &:nth-child(4){
        margin-top: -150px;
      }
      &:nth-child(5){
        margin-top: -90px;
      }
    }
    &__result{
      margin-top: -150px;
    }
  }
}

.step-card{
  &__top{
    display: flex;
    align-items: flex-end;
  }
  &__num{
    flex-shrink: 0;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    &:after{
      content: '';
      display: block;
      width: 3px;
      height: 65px;
      background-color: $primary;
      flex-shrink: 0;
    }
    &:before{
      content: '';
      order: 20;
      width: 11px;
      height: 11px;
      display: block;
      border: 3px solid $primary;
      border-radius: 50%;
    }
    span{
      @include center;
      background-color: $primary;
      border-radius: 50%;
      color: #fff;
      font-weight: 900;
      font-size: 14px;
      line-height: 18px;
      letter-spacing: 0.2em;
      width: 50px;
      height: 50px;
    }
  }
  &__arrow{
    width: calc(100% - 34px);
    display: block;
    margin-left: -16px;
    margin-bottom: 4px;
  }
  &__content{
    margin-top: 30px;
  }
  &__desc{
    font-size: 15px;
    line-height: 160%;
    color: rgba(0, 0, 0, 0.75);
    margin-top: 11px;
  }
  &__btn{
    margin-top: 23px;
  }
}

.production{
  padding: 30px 0 60px;
  overflow: hidden;
  .profitable__grid{
    margin-top: 0;
  }
  .profitable__icon{
    width: 59px;
  }
  .profitable__content{
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}



@media screen and (max-width: 1800px){
  .clients{
    .swiper{
      &-button1{
        right: 0;
      }
      &-slide{
        width: calc(100% / 7);
      }
    }
    &__slider{
      padding: 0 60px 0 0;
    }
  }

  .counts{
    &__col1{
      padding-top: 25%;
    }
    &__item2{
      grid-column: 1/-1;
    }
  }
}



@media screen and (max-width: 1500px){
  .about-page{
    &__grid{
      grid-template-columns: 35% calc(65% - 32px);
    }
    &__content{
      padding: 0;
    }
  }

  .team{
    &__grid{
      margin-top: 100px;
    }
    @media screen and (min-width: 1200px){
      .team-card{
        &:nth-child(6){
          .team-card__badge{
            left: unset;
            right: -23px;
          }
        }
      }
    }
  }

  .how-work{
    &__grid{
      margin-top: 40px;
      grid-template-columns: repeat(4,calc(25% - 48px / 4));
    }
  }
}


@media screen and (max-width: 1400px){
  .team{
    &__grid{
      grid-template-columns: repeat(5,calc(20% - 80px / 5));
    }
  }

  .counts{
    &__img{
      bottom: 240px;
    }
  }
}


@media screen and (max-width: 1300px){
  .team{
    &__grid{
      grid-template-columns: repeat(4,calc(25% - 15px));
    }
  }
}



@media screen and (max-width: 1200px){
  .about-page{
    &__grid{
      grid-template-columns: 100%;
      margin-top: 20px;
    }
    &__squirrel{
      display: none;
    }
    &__logo{
      position: static;
      margin: 10px 0;
    }
    &__img{
      @include fullScreen;
    }
  }

  .clients{
    .swiper{
      &-slide{
        width: calc(100% / 6);
      }
    }
  }

  .counts{
    padding-top: 30px;
    &__grid{
      grid-template-columns: 100%;
      gap: 64px;
    }
    .counts{
      &__col2{
        display: block;
      }
      &__img{
        position: static;
        transform: unset;
        margin: -80px auto 0;
        display: block;
      }
    }
  }

  .how-work{
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - 32px / 3));
    }
  }
}


@media screen and (max-width: 992px){
  .work-with{
    &__title{
      margin-top: 5px;
    }
    &__desc{
      text-align: left;
      margin: 5px 0 0;
    }
  }

  .clients{
    .swiper{
      &-slide{
        width: calc(100% / 4);
      }
    }
  }

  .team{
    &__title{
      &-bottom{
        margin-top: 3px;
        text-align: left;
      }
    }
    &__desc{
      text-align: left;
    }
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - 40px / 3));
    }
  }

  .counts{
    &__col1{
      padding: 25px 0;
    }
    &__col2{
      padding: 25px;
    }
    &__items{
      gap: 40px;
    }
    &__countries,
    &__item2{
      display: block;
      padding: 0;
      &-num{
        height: unset;
        font-size: 72px;
        margin-top: unset;
        transform: unset;
        margin-bottom: 0;
        display: block;
      }
      br{
        display: none;
      }
    }
    &__item{
      &-num{
        font-size: 48px;
      }
    }
  }
}


@media screen and (max-width: 768px){
  .clients{
    .swiper{
      &-slide{
        width: calc(100% / 3);
      }
    }
  }

  .how-work{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 8px));
    }
  }
}


@media screen and (max-width: 600px){
  .clients{
    overflow: hidden;
    &__slider{
      padding: 0;
    }
    .swiper{
      &-container{
        overflow: unset;
      }
      &-slide{
        width: 200px;
      }
      &-button1{
        display: none;
      }
    }
  }

  .team{
    &__grid{
      grid-template-columns: 100%;
      gap: 30px;
      margin-top: 40px;
    }
  }

  .team-card{
    &__badge{
      position: relative;
      left: -15px;
      width: calc(100% + 30px);
      bottom: unset;
    }
  }

  .how-work{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .step-card{
    &__num{
      &:before,
      &:after{
        display: none;
      }
    }
    &__arrow{
      display: none;
    }
    &__content{
      margin-top: 10px;
    }
  }
}
