.main{
  display: flex;
  flex-direction: column;
}

.designers{
  padding: 0;
  background-color: $grayBg;
  position: relative;
  overflow: hidden;
  &:before{
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 70%;
    height: 8px;
    background-color: $primary;
  }
  &__grid{
    display: grid;
    grid-template-columns: 32.8% calc(67.2% - 20px);
    gap: 20px;
  }
  &__content{
    padding: 30px 0 70px;
    position: relative;
  }
  &__img{
    min-height: 100%;
    position: relative;
    z-index: 2;
    img{
      height: 100%;
      width: calc(100% + 100px);
      display: block;
      border-radius: 0 0 0 50px;
      object-fit: cover;
    }
  }
  &__title{
    margin-top: 61px;
    font-size: 36px;
    line-height: 140%;
    text-transform: uppercase;
  }
  &__desc{
    margin-top: 21px;
    text-transform: uppercase;
    max-width: 358px;
  }
  &__sale{
    display: flex;
    align-items: flex-end;
    text-transform: uppercase;
    color: $primary;
    margin: -88px 0 0 154px;
    &-percent{
      font-size: 128px;
      line-height: 160%;
    }
    &-title{
      padding: 0 0 51px 5px;
    }
  }
  &__logo{
    position: absolute;
    bottom: 8px;
    right: 42px;
    width: 357px;
    max-width: 85%;
  }
}

.form2{
  @media screen and (min-width: 900px){
    .form__content{
      max-width: 570px;
    }
    .form__inputs{
      display: grid;
      grid-template-columns: repeat(3,calc(100% / 3 - 10px));
      gap: 20px 15px;
    }
    .form__item{
      max-width: unset;
    }
  }

  &__desc{
    margin-top: 10px;
    color: #979797;
  }
}

.profitable{
  margin-top: 60px;
  .container{
    position: relative;
  }
  &__squirrel{
    width: 247px;
    position: absolute;
    right: 185px;
    bottom: 100%;
  }
  &__grid{
    margin-top: 28px;
    display: grid;
    grid-template-columns: repeat(2,50%);
  }
  &__img{
    min-height: 100%;
    img{
      object-fit: cover;
      height: 100%;
      width: 100%;
      display: block;
      border-radius: 0 0 0 60px;
    }
  }
  &__content{
    background-color: $primary;
    color: #fff;
    border-radius: 0 0 0 60px;
    width: calc(100% + 50px);
    margin-left: -50px;
    padding: 67px 55px 113px 118px;
  }
  &__icon{
    width: 91px;
    display: block;
  }
  &__text{
    margin-top: 40px;
    max-width: 684px;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 20px;
    margin-top: 48px;
  }
  &__item{
    display: flex;
    align-items: flex-start;
    font-size: 16px;
    line-height: 160%;
    img{
      width: 30px;
      display: block;
      margin-right: 17px;
    }
  }
}



@media screen and (max-width: 1200px) {
  .designers {
    &:before{
      display: none;
    }
    &__grid {
      grid-template-columns: 100%;
    }
    &__img{
      border-top: 4px solid $primary;
      height: unset;
      min-height: unset;
      @include fullScreen;
      padding: 0;
      img{
        width: 100%;
      }
    }
  }

  .form2{
    .form__text{
      margin-bottom: 0;
    }
    &__desc{
      margin-bottom: 28px;
    }
  }

  .profitable{
    &__grid{
      grid-template-columns: 100%;
      @include fullScreen;
      padding: 0;
      border-radius: 0 0 0 60px;
      background-color: $primary;
    }
    &__content{
      width: 100%;
      margin: 0;
      padding: 30px 15px 60px;
    }
  }
}


@media screen and (max-width: 900px){
  .form{
    &__inputs{
      flex-direction: column;
    }
    &__item{
      margin-right: 0 !important;
    }
  }
}


@media screen and (max-width: 600px){
  .designers{
    &__title{
      margin-top: 30px;
      font-size: 28px;
    }
    &__sale{
      margin: -30px 0 0;
      &-percent{
        font-size: 90px;
      }
      &-title{
        padding-bottom: 38px;
      }
    }
  }

  .profitable{
    &__squirrel{
      display: none;
    }
    &__items{
      grid-template-columns: 100%;
    }
    &__item{
      align-items: center;
      br{
        display: none;
      }
    }
  }
}
