.inner-page{
  border-top: 1px solid #D5D5D5;
  margin-top: 33px;
  padding-top: 30px;
}

.contacts-page{
  background-color: $grayBg;
  &__top{
    padding-bottom: 40px;
    border-bottom: 1px solid #D7D7D7;
  }
  &__phone{
    display: flex;
    justify-content: center;
    padding: 35px 0;
    border-bottom: 1px solid #D7D7D7;
    position: relative;
  }
  &__squirrel{
    position: absolute;
    right: 143px;
    top: -74px;
    width: 125px;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 12px));
    gap: 36px 18px;
    padding: 60px 0 130px;
  }
  &__item{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__icon{
    height: 40px;
    img{
      width: auto;
      height: 100%;
      display: block;
    }
  }
  &__title{
    margin: 24px 0 13px;
  }
  &__address{
    margin-top: 12px;
    max-width: 550px;
  }
  &__link{
    color: $primary;
    text-decoration: underline;
    &:hover{
      text-decoration: none;
    }
  }
}

.map{
  height: 670px;
}



@media screen and (max-width: 992px){
  .inner-page{
    padding-top: 20px;
    margin-top: 20px;
  }

  .contacts-page{
    &__top{
      padding-bottom: 20px;
    }
    &__phone{
      padding: 20px 0;
    }
    &__items{
      padding: 20px 0 60px;
      grid-template-columns: repeat(2,calc(50% - 9px));
    }
  }
}


@media screen and (max-width: 600px){
  .contacts-page{
    &__squirrel{
      display: none;
    }
    &__items{
      grid-template-columns: 100%;
    }
  }
}
